<script setup>
import { onMounted, ref } from "vue";
import useYaMapCreateMarker from "@/use/useYaMapCreateMarker";

const props = defineProps({
    coordinates: {
        type: Array,
        default: [38.975313, 45.035470]
    },
    title: {
        type: String,
        default: ''
    },
    subtitle: {
        type: String,
        default: ''
    },
    imagePath: {
        type: String,
        default: ''
    }
})
const elem = ref(null)

onMounted(async () => {
    await ymaps3.ready
    const { YMap, YMapDefaultSchemeLayer, YMapControls, YMapDefaultFeaturesLayer, YMapMarker } = ymaps3
    const { YMapZoomControl } = await ymaps3.import('@yandex/ymaps3-controls@0.0.1')
    const location = {
        center: props.coordinates,
        zoom: 12
    }

    const map = new YMap(elem.value, { location })
    const marker = useYaMapCreateMarker(props.imagePath)
    marker.onclick = () => map.update({ location: { ...location, duration: 400 }})

    map.addChild(new YMapDefaultSchemeLayer())
    map.addChild(new YMapDefaultFeaturesLayer())
    map.addChild(new YMapControls({ position: 'right' }).addChild(new YMapZoomControl({})))
    map.addChild(new YMapMarker({ coordinates: props.coordinates }, marker))
})
</script>

<template>
    <div class="ya-map" ref="elem"></div>
</template>

<style scoped lang="scss">
.ya-map {
    width: 100%;
    height: 100%;
}
</style>