function dist(lat1, lon1, lat2, lon2) {
	const R = 6371; // Радиус Земли в километрах
	const dLat = (lat2 - lat1) * (Math.PI / 180);
	const dLon = (lon2 - lon1) * (Math.PI / 180);
	const a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
		Math.cos(lat1 * (Math.PI / 180)) * Math.cos(lat2 * (Math.PI / 180)) *
		Math.sin(dLon / 2) * Math.sin(dLon / 2);
	const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
	return R * c; // Дистанция в километрах
}

export default function (place, places, numNearby) {
	const filteredPlaces = places.filter(p => p.id !== place.id);
	const distances = filteredPlaces.map(p => ({
		point: p,
		distance: dist(place.coordinates[1], place.coordinates[0], p.coordinates[1], p.coordinates[0])
	}))
	distances.sort((a, b) => a.distance - b.distance);

	return distances.slice(0, numNearby).map(item => item.point);
}