<script setup>
import { useRoute } from 'vue-router'
import {onActivated, onBeforeMount, ref} from "vue";
import api from "@/api";
import BreadCrumbs from "@/components/BreadCrumbs.vue";
import FsLightbox from "fslightbox-vue/v3"
import YaMap from "@/components/YaMap.vue";
import CardPlace from "@/components/CardPlace.vue";
import useFindNearbyPlaces from "@/use/useFindNearbyPlaces";

const route = useRoute()
const place = ref(null)
const placesNearby = ref(null)

const breadcrumbs = [
    { name: 'Главная', path: '/' },
    { name: 'Парки и скверы в Краснодаре', path: '/places' },
]

let activeSlide = 1
const isOpenLightBox = ref(false)
const toggleLightBox = (numSlide) => {
    activeSlide = numSlide
    isOpenLightBox.value = !isOpenLightBox.value
}

onActivated(() => {
    window.scrollTo({
        top: 0,
        left: 0,
        behavior: "smooth"
    })

    if (place.value?.name) {
        document.title = place.value?.name
    }
})

onBeforeMount(async () => {
    try {
        place.value = await api.place.getSingle({
            region: 'krasnodarskiy-kray',
            city: 'krasnodar',
            category: 'parki-i-skvery',
            place: route.params.place
        })

        // console.log(place.value)

        const getPlaces = await api.place.getAll({
            region: 'krasnodarskiy-kray',
            city: 'krasnodar'
        })

        placesNearby.value = useFindNearbyPlaces(place.value, getPlaces.places, 3)
        document.title = place.value.name
    } catch (e) {
        console.log(e)
    }
})

</script>

<template>
    <div class="page-wrapper">
        <section class="container" v-if="place">
            <bread-crumbs :links="breadcrumbs" :current="place.name"/>
            <h1 class="page-title page-title_h1">{{ place.name }}</h1>
            <div class="place__gallery">

                <template v-for="(image, index) in place.images" :key="image">
                    <img
                        class="place__gallery-image"
                        :src="`${ image }`"
                        :alt="`${ place.name } ${ index + 1 }`"
                        @click="toggleLightBox(index + 1)"
                    >
                </template>

                <div class="place__gallery-map">
                    <div class="place__gallery-map-title">
                        <address>{{ place.country.name }}, {{ place.region.name }}, г. {{ place.city.name }}, {{ place.address }}</address>
                    </div>
                    <ya-map
                        :coordinates="place.coordinates"
                        :title="place.name"
                        :subtitle="place.address"
                        :imagePath="place.images[0]"
                    />
                </div>
            </div>
            <fs-lightbox
                :slide="activeSlide"
                :toggler="isOpenLightBox"
                :sources="place.images"
                type="image"
            />
        </section>
        <section class="place__nearby page-section" v-if="placesNearby">
            <div class="container">
                <h2 class="page-title page-title_h2">Места рядом</h2>
            </div>
            <div class="place__nearby-grid container">
                <template v-for="place in placesNearby" :key="place.id">
                    <card-place :data="place"/>
                </template>
            </div>
        </section>
    </div>
</template>

<style lang="scss">
.place {
    &__gallery {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
        grid-auto-columns: 1fr;
        //grid-auto-rows: minmax(250px, 1fr);
        gap: 5px;
    }

    &__gallery-image {
        width: 100%;
        aspect-ratio: 4/3;
        object-fit: cover;
        box-shadow: 2px 2px 20px -8px rgba(0,0,0,0.4);
        border-radius: 5px;
        cursor: pointer;
    }

    &__gallery-image:first-child {
        grid-row: 1 / 3;
        grid-column: 1 / 3;
        height: 360px;
    }

    &__gallery-map {
        grid-row: 1 / 3;
        grid-column: 3 / -1;
        border: 1px solid #ccc;
        box-shadow: 2px 2px 20px -8px rgba(0,0,0,0.4);
        border-radius: 5px;
        overflow: hidden;
        display: flex;
        flex-direction: column;
    }

    &__gallery-map-title {
        background-color: #ffffff;
        font-weight: bold;
        border-bottom: 1px solid #eee;
        padding: 20px;
        box-shadow: 2px 2px 20px -8px rgba(0,0,0,0.2);
        z-index: 1;
    }

    &__nearby {

    }

    &__nearby-grid {
        display: flex;
        gap: 40px
    }
}
</style>